.Registerok-header{
  background-color: rgb(255, 255, 255);
  height: 11vh;
  padding: 0 25vh;
}
.Registerok-Content-1{
  padding: 0 26vh;
}
.Registerok-Content-2{
  padding: 0 26vh;
}
.Registerok-Content-3{
  margin: 2vh 26vh 0;
  background-color: #fff;
  height: 60vh;
}
.Registerok-Content-2 img{
    width: 2.5vh;
    height: 2.5vh;
    float: left;
    margin-top: 1.5vh;
    margin-left: 20vh;
}
.header-right{
  float: right;
}
.header-left-ziti{
  float: 'left';
  height: 11vh;
  width: 40vh;
  line-height: 11vh;
  font-size: 5vh;
  margin-left: 1vh;
  font-family: 'myFont1';
  background-image: linear-gradient(to bottom , rgb(255, 156, 14), rgb(240, 123, 13));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.Registerok-Content-2-col-1{
  background-color: rgb(255, 156, 14);
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  -webkit-clip-path: polygon(0 0, 98% 0, 100% 50%, 98% 100%, 0 100%);
  clip-path: polygon(0 0, 98% 0, 100% 50%, 98% 100%, 0 100%);
  font-size: 2.5vh;
  font-weight: bold;
  color: #ffffff;
}
.Registerok-Content-2-col-1-ziti{
float: left;
margin-top: 1vh;
}
.Registerok-Content-2-col-2-ziti{
  float: left;
  margin-top: 1vh;
}
.Registerok-Content-2-col-3-ziti{
  float: left;
  margin-top: 1vh;
}
.Registerok-Content-2-col-2{
  background-color: rgb(255, 156, 14);
  margin-left: -3px;
  -webkit-clip-path: polygon(0 0, 98% 0, 100% 50%, 98% 100%, 0 100%, 2% 50%);
  clip-path: polygon(0 0, 98% 0, 100% 50%, 98% 100%, 0 100%, 2% 50%);  
  font-size: 2.5vh;
  font-weight: bold;
  color: rgb(255, 255, 255);
}
.Registerok-Content-2-col-3{
  background-color: rgb(255, 156, 14);
  margin-left: -3px;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%, 2% 50%);
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%, 2% 50%);
  font-size: 2.5vh;
  font-weight: bold;
  color: rgb(255, 255, 255);
}
:where(.css-dev-only-do-not-override-18iikkb).ant-layout-footer{
  padding: unset;
}